.styled-checkbox {
  position: absolute; // take it out of document flow
  opacity: 0; // hide it

  & + label {
    display: flex;
    position: relative;
    cursor: pointer;
    padding: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: -0.22px;
    color: #fcfffc;
  }

  // Box.
  & + label:before {
    content: "";
    margin-top: 2px;
    margin-right: 8px;
    display: inline-block;
    vertical-align: text-top;
    width: 14px;
    height: 14px;
    background: transparent;
    border: 1px solid white;
  }

  // Box hover
  // &:hover + label:before {
  //   background: #f35429;
  // }

  // Box focus
  &:focus + label:before {
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);
  }

  // Box checked
  &:checked + label:before {
    background: transparent;
  }

  // Disabled state label.
  &:disabled + label {
    color: #b8b8b8;
    cursor: auto;
  }

  // Disabled box.
  &:disabled + label:before {
    box-shadow: none;
    background: #ddd;
  }

  // Checkmark. Could be replaced with an image
  &:checked + label:after {
    content: "";
    position: absolute;
    left: 2px;
    top: 6px;

    width: 2px;
    height: 2px;
    margin-top: 2px;
    background: white;
    box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white,
      4px -6px 0 white, 4px -8px 0 white;
    transform: rotate(45deg);
  }
}
