<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="modal-header">
            <slot name="header">
              <div @click="$emit('close')" class="icon">
                <ModalCloseIcon :dark="true" />
              </div>
            </slot>
          </div>

          <div class="modal-body">
            <slot name="body">
              <div class="logo">
                <img src="../../assets/logo.png" alt="Nvoko Logo" />
              </div>
              <h2>Welcome Back!</h2>
              <div class="inputs">
                <div class="form-group">
                  <input
                    v-model="email"
                    id="email"
                    type="email"
                    class="text-input dark"
                    placeholder="Enter your email"
                  />
                </div>
                <div class="form-group">
                  <input
                    v-model="password"
                    id="password"
                    type="password"
                    class="text-input dark"
                    placeholder="Type the password"
                  />
                </div>
              </div>
              <button
                @click="onLoginClick()"
                class="primary"
                :disabled="!enableLoginButton || isLoading"
              >
                {{ loginButtonText }}
              </button>
            </slot>
          </div>

          <div class="modal-footer">
            <slot name="footer"> </slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import gql from "graphql-tag";
import ModalCloseIcon from "@/assets/icons/Modal-Close.vue";

const LOGIN = gql`
  mutation login($email: String!, $password: String!) {
    login(email: $email, password: $password) {
      token
      user {
        email
        full_name
        id
        status
      }
    }
  }
`;

export default {
  name: "LoginModal",
  components: {
    ModalCloseIcon
  },
  data() {
    return {
      email: null,
      password: null,
      isLoading: false
    };
  },
  computed: {
    enableLoginButton() {
      return this.email && this.password;
    },
    loginButtonText() {
      return this.isLoading ? "Loading..." : "Login";
    }
  },
  methods: {
    async login() {
      const result = await this.$apollo.mutate({
        mutation: LOGIN,
        variables: {
          email: this.email.trim(),
          password: this.password
        }
      });
      const { login } = result.data;
      this.$store.commit("account/loginSuccess", login);
    },
    async onLoginClick() {
      try {
        this.isLoading = true;
        await this.login();
        await this.$router.push({ name: "Talent" });
      } catch (e) {
        alert(e.message || e);
      } finally {
        this.isLoading = false;
      }
    }
  }
};
</script>

