    align-items: center;

    .arrow-down {
      content: "";
      margin: 5px 0 0 7px;
      border: 5px solid transparent;
      border-color: #3b3b3b transparent transparent transparent;
    }
  }

  .open {
    border-radius: 20px 20px 0 0;
