const state = {
  apolloToken: null,
  user: null,
  artist: null
};

const actions = {};

const mutations = {
  loginSuccess(state, payload) {
    const { token, user } = payload;
    state.user = user;
    state.apolloToken = token;
  },
  registerSuccess(state, payload) {
    const { token, user } = payload;
    state.user = user;
    state.apolloToken = token;
  },
  addArtist(state, payload) {
    // const { token, user } = payload;
    state.artist = payload;
  },
  logout(state) {
    state.apolloToken = null;
    state.user = null;
    state.artist = null;
  }
};

const getters = {
  getApolloToken(state) {
    return state.apolloToken;
  },
  getUserId(state) {
    return state.user.id;
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
};
